@use "../../../styles/variables" as *;
@use "sass:color";

.form-container {
  display: flex;
  flex-direction: column;
  gap: 2rem;

  .headline {
    margin-bottom: 1rem;
  }

  .validation,
  .error-message {
    color: $validation-red;
  }

  form {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2.1rem;
    align-content: space-between;
    height: 100%;
    max-height: 50vh;
    overflow: auto;

    @include scroll-bar;

    //checkbox//

    label:has(input[type="checkbox"]) {
      display: flex;
      gap: 0.5rem;
      cursor: pointer;
    }

    input[type="checkbox"] {
      appearance: none;
      margin: 0;

      &:before {
        content: "";
        width: 1.6rem;
        height: 1.6rem;
        display: block;
        border: 0.1rem solid $dark-grey;
        border-radius: 0.4rem;
        background-color: white;
        transition: 0.3s all;
      }

      &:checked {
        &:before {
          background-color: $dark-blue !important;
          border-color: $dark-blue;
          background: url("../../../../public//icons//checkmark_white.svg")
            no-repeat center;
          background-size: 1rem !important;
        }

        &:after {
          content: "";
          position: absolute;
          top: 0.2rem;
          left: 0.4rem;
          width: 0.5rem;
          height: 0.8rem;
          border: solid white;
          border-width: 0 0.2rem 0.2rem 0;
          transform: rotate(45deg);
        }
      }
    }

    //Dropdown//

    :global(.dropdown__indicator-separator) {
      display: none;
    }

    :global(.dropdown__clear-indicator) {
      display: none;
    }

    :global(.dropdown__indicator) {
      svg {
        display: none;
      }

      &:after {
        content: "";
        width: 1.5rem;
        height: 1.5rem;
        background-size: 1.5rem !important;
        content: "";
        background: url("../../../../public/icons/arrow-down.svg") no-repeat
          center;
        rotate: 0deg;
        transition: 0.3s all;
      }
    }

    :global(.dropdown__control--menu-is-open) {
      :global(.dropdown__indicator) {
        &:after {
          rotate: 180deg;
        }
      }
    }

    :global(.dropdown) {
      height: 100%;
      :global(.dropdown__menu) {
        box-shadow: 0 0 20px 0px hsla(0, 0%, 0%, 0.1),
          0 4px 11px hsla(0, 0%, 0%, 0.1);
        z-index: 2;
        top: 3.7rem;

        :global(.dropdown__menu-list) {
          @include scroll-bar;
          max-height: 14rem;
        }

        :global(.dropdown__option) {
          transition: 0.3s all;
          padding-top: 1rem;
          padding-bottom: 1rem;
          color: $dark-forest-green;
        }

        :global(.dropdown__option--is-focused),
        :global(.dropdown__option--is-selected) {
          background-color: transparent;
          color: $lime-green;
        }
      }
    }

    .error-message {
      margin-top: 1rem;
      position: relative;
      z-index: 1;

      &.general-error {
        text-align: center;
        grid-column: 1 / 3;
      }
    }

    .submit-button {
      width: 100%;
      grid-column: 1/3;
      margin-top: 0.5rem;
    }
  }
}
